import subject_data from './subject-audio-data'

export const initialState = {
  user: null,
  playlists: ['English','History','Chemistry','Biology'],
  subject_audio: {},
  playing: false,
  item: 'English',
  curr_song: null,
  isloading: false
};


const reducer = (state, action) => {
  
  switch (action.type) {
    case "SET_SUBJECT_AUDIO":
      return {
        ...state,
        subject_audio: action.subject_audio,
        isloading: false
      };
    case "Loading":
      return {
        ...state,
        isloading: action.loading
      };
    case "SET_USER":
      return {
        ...state,
        user: action.user
      };
    case "SET_ITEM":
      return {
        ...state,
        item: action.item
      }
    case "SET_SONG":
      return {
        ...state,
        curr_song: action.song,
        playing: false
      }
    case "SET_PLAYING":
      return {
        ...state,
        playing: action.playing
      }
    case "SET_AUDIO":
    let newplaylist = state.subject_audio
    console.log(action)
    console.log(newplaylist)
    newplaylist[action.name].push({'title':action.title,'teacher':'ajay','audio':'english1.mp3'})
    return{
      ...state,
      subject_audio: newplaylist
    }
    default:
        return state;

  };
}

export default reducer;
